import React from "react";
import './IDBIE1.css';
import ImportFile from './ImportFile';
import ExportFile from './ExportFile';
import DatabaseInfo from './DatabaseInfo';
import progressCallback from './progressCallback';


export default function IDBIE1() {

    return < >
        <div className="column">
            <h3>Export</h3>
            <ExportFile progressCallback={progressCallback}  />
        </div>

        <div className="column">
            <h3>Import</h3>
            <ImportFile progressCallback={progressCallback} />
        </div>
        
        <div className="column">
            <h3>Database Info</h3>
            <DatabaseInfo />
        </div>
    </>;
}
